:root {
  --theme-color: #1cb9c8;
  --title-color: #010f1c;
  --body-color: #535b61;
  --smoke-color: #f2f2f2;
  --smoke-dark: #dde2ee;
  --black-color: #000000;
  --white-color: #ffffff;
  --light-color: #72849b;
  --border-color: #cfd3dc;
  --title-font: "Inter", sans-serif;
  --body-font: "Inter", sans-serif;
  --main-container: 1380px;
  --container-gutters: 24px;
  --section-space: 50px;
  --section-title-space: 70px;
  --ripple-ani-duration: 5s;
}
body {
  font-family: "Nunito Sans", sans-serif;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Montserrat", sans-serif;
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

